html {
  --text-color: #00acdc;
  --logo-size: 40px;
  --color: #000;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  height: var(--logo-size);
  width: var(--logo-size);
  text-decoration: none;
}

.nav_list {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav_link {
  height: 60px;
  color: var(--color);
  align-items: center;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_list :not(:first-child) {
  margin-left: 20px;
}

.is-active, .nav_link:hover {
  color: var(--text-color);
}

a.is-active:after {
  content: "";
  background-color: var(--text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}
